import { Box, Divider, Skeleton, Stack, Typography } from "@mui/material";
import { useWidth } from "../../../configs/breakpoints";
import { useTranslation } from "../../../configs/translation";
import { eNum } from "../../../models/core/my-js";
import AppHtmlViewer from "../../../layouts/AppHtmlViewer";
import AppProductCartBtn from "../../../layouts/AppProductCartBtn";
import AppProductFavBtn from "../../../layouts/AppProductFavBtn";

const ProductTitle = ({ productId, product }) => {
  const { t, forLocale } = useTranslation();
  const isPriced = product?.price && product?.price > 0;
  const isOutStock = !product?.stock || product?.stock === "0";
  const { widthDevice } = useWidth();

  if (!product) {
    return (
      <Stack spacing={3} px={1} mt={1}>
        <Skeleton variant="rectangular" width="90%" height={25} />
        <Skeleton variant="rectangular" width="40%" height={15} />

        <Divider />

        <Skeleton variant="rectangular" width="50%" height={15} />
        <Skeleton variant="rectangular" width="50%" height={15} />
        <Skeleton variant="rectangular" width="50%" height={15} />
        <Skeleton variant="rectangular" width="75%" height={50} />

        <Divider />

        <Skeleton variant="rectangular" width="75%" height={30} />
      </Stack>
    );
  } else {
    return (
      <Stack spacing={3} px={1} mt={1}>
        <Stack spacing={3} px={1}>
          <Typography variant="h5" sx={{ fontWeight: "bold" }}>
            {product?.[forLocale({ ar: "name_alt" }, "name")]}
          </Typography>

          {isPriced && (
            <Box>
              <Typography
                variant="h4"
                component="span"
                color={isOutStock ? "primary" : "secondary"}
                sx={{ fontWeight: "bold" }}
              >
                {eNum.readable(parseFloat(product?.price), 0)}
              </Typography>
              <Typography
                variant="subtitle1"
                component="span"
                color={isOutStock ? "primary" : "secondary"}
                sx={{ px: 0.25 }}
              >
                {t("currency")}
              </Typography>
            </Box>
          )}
        </Stack>

        {!product?.sku &&
        !product?.model &&
        !product?.part_no &&
        !product?.brief ? null : (
          <Divider />
        )}

        <Stack spacing={3} px={1}>
          {product?.sku && (
            <Box>
              <Typography variant="subtitle1" component="span">
                {t("sku") + ":"}
              </Typography>
              <Typography
                variant="h6"
                component="span"
                sx={{ fontWeight: "bold", px: 1 }}
              >
                {product?.sku}
              </Typography>
            </Box>
          )}

          {product?.model && (
            <Box>
              <Typography variant="subtitle1" component="span">
                {t("model") + ":"}
              </Typography>
              <Typography
                variant="h6"
                component="span"
                sx={{ fontWeight: "bold", px: 1 }}
              >
                {product?.model}
              </Typography>
            </Box>
          )}

          {product?.part_no && (
            <Box>
              <Typography variant="subtitle1" component="span">
                {t("part_no") + ":"}
              </Typography>
              <Typography
                variant="h6"
                component="span"
                sx={{ fontWeight: "bold", px: 1 }}
              >
                {product?.part_no}
              </Typography>
            </Box>
          )}

          {product?.brief && (
            <Box>
              <AppHtmlViewer
                html={product?.[forLocale({ ar: "brief_alt" }, "brief")]}
              />
            </Box>
          )}
        </Stack>

        {isPriced && <Divider />}

        {isPriced && (
          <Stack
            direction={widthDevice("column", null, "row")}
            alignItems={widthDevice("center", "start")}
            spacing={2}
          >
            <AppProductCartBtn
              productId={productId}
              product={product}
              full={true}
              size={"large"}
            />
            <AppProductFavBtn
              productId={productId}
              full={true}
              size={"large"}
            />
          </Stack>
        )}
      </Stack>
    );
  }
};

export default ProductTitle;
