//
import React from "react";
import {
  AppBar,
  Toolbar,
  Typography,
  IconButton,
  Button,
  useTheme,
  Container,
  Box,
  Badge,
  Autocomplete,
  TextField,
  ButtonGroup,
  Stack,
  Link,
} from "@mui/material";
import {
  ArrowBackTwoTone,
  ArrowForwardTwoTone,
  BeenhereTwoTone,
  CategoryTwoTone,
  FavoriteTwoTone,
  HomeTwoTone,
  LanguageTwoTone,
  SearchTwoTone,
  ShoppingCartTwoTone,
  StorefrontTwoTone,
} from "@mui/icons-material";
import { useNavigate } from "react-router-dom";
import { useTranslation } from "../configs/translation";
import { useBreakpoints, useWidth } from "../configs/breakpoints";
import MyImage from "../components/MyImage";
import LogoIcon from "../assets/images/logoIcon.png";
import Logo from "../assets/images/logo.png";
import { eList } from "../models/core/my-js";
import { useMyCart } from "../models/MyCart";
import { useMyFav } from "../models/MyFav";
//

const AppHeader = ({ title, main }) => {
  const { only } = useBreakpoints();
  const navigate = useNavigate();
  const theme = useTheme();
  const { t, locale, locales, setLocale, forDir } = useTranslation();
  const { cartCount } = useMyCart();
  const { favCount } = useMyFav();
  const { widthDevice } = useWidth();

  if (only("mobile")) {
    if (main === true) {
      return MobileMainHeader({
        navigate,
        theme,
        t,
        locale,
        locales,
        setLocale,
        forDir,
      });
    } else {
      return MobileSubHeader({
        title,
        navigate,
        theme,
        locale,
        locales,
        setLocale,
        forDir,
      });
    }
  } else {
    return DesktopHeader({
      navigate,
      theme,
      t,
      locale,
      locales,
      setLocale,
      forDir,
      cartCount,
      favCount,
      widthDevice,
    });
  }
};
export default AppHeader;

const MobileMainHeader = ({
  navigate,
  theme,
  t,
  locale,
  locales,
  setLocale,
  forDir,
}) => {
  return (
    <AppBar position="static" variant="dense" color="white" sx={{ mb: 1 }}>
      <Toolbar
        variant="dense"
        sx={{
          borderBottomWidth: 1,
          borderBottomStyle: "solid",
          borderBottomColor: theme.palette.light.main,
          px: 0.5,
        }}
        disableGutters
      >
        <MyImage
          src={LogoIcon}
          alt={t("welcome_greeting")}
          width={40}
          height={40}
        />
        <Typography
          variant="h6"
          textAlign={"start"}
          sx={{ flexGrow: 1, px: 1 }}
          noWrap
        >
          {t("welcome_greeting")}
        </Typography>
        <Button
          onClick={() => {}}
          size="small"
          variant="contained"
          color="light"
          sx={{ p: 0.5 }}
          disableElevation
        >
          <SearchTwoTone fontSize="medium" />
        </Button>
        <IconButton
          onClick={() => {
            var lk = Object.keys(locales);
            var l = lk.indexOf(locale);
            lk.splice(l, 1);

            setLocale(lk[0]);
          }}
          size="small"
          variant="outlined"
          color="secondary"
        >
          <LanguageTwoTone />
        </IconButton>
      </Toolbar>
    </AppBar>
  );
};
const MobileSubHeader = ({
  title,
  navigate,
  theme,
  locale,
  locales,
  setLocale,
  forDir,
}) => {
  return (
    <AppBar position="static" variant="dense" color="white" sx={{ mb: 1 }}>
      <Toolbar
        variant="dense"
        sx={{
          borderBottomWidth: 1,
          borderBottomStyle: "solid",
          borderBottomColor: theme.palette.light.main,
          px: 0.5,
        }}
        disableGutters
      >
        <Button
          onClick={() => {
            navigate(-1);
          }}
          size="small"
          variant="contained"
          color="light"
          sx={{ p: 0.5 }}
          disableElevation
        >
          {forDir(
            <ArrowBackTwoTone fontSize="small" />,
            <ArrowForwardTwoTone fontSize="small" />
          )}
        </Button>
        <Typography
          variant="subtitle1"
          textAlign={"center"}
          sx={{ flexGrow: 1, px: 1 }}
          noWrap
        >
          {title}
        </Typography>
        <IconButton
          onClick={() => {
            var lk = Object.keys(locales);
            var l = lk.indexOf(locale);
            lk.splice(l, 1);

            setLocale(lk[0]);
          }}
          size="small"
          color="secondary"
        >
          <LanguageTwoTone />
        </IconButton>
      </Toolbar>
    </AppBar>
  );
};

const DesktopHeader = ({
  navigate,
  theme,
  t,
  locale,
  locales,
  setLocale,
  forDir,
  cartCount,
  favCount,
  widthDevice,
}) => {
  return (
    <>
      <AppBar position="static" variant="dense" color="white">
        <Container disableGutters fixed>
          <Toolbar
            variant="dense"
            sx={{
              py: 1.75,
              gap: widthDevice(null, 2, 3, 4),
              height: 75,
            }}
            disableGutters
          >
            <MyImage
              src={Logo}
              alt={t("welcome_greeting")}
              width={"auto"}
              height={"100%"}
              style={{ borderRadius: 4 }}
            />
            <Box height={"100%"} sx={{ flexGrow: 1 }}>
              <SearchBar t={t} fullWidth={true} height={"100%"} />
            </Box>
            <Stack direction={"column"}>
              <Link href="tel:201023966756" underline="hover">
                <Typography className="ltr enFonts" noWrap>
                  {"+2 0102 396 6756"}
                </Typography>
              </Link>
              <Link href="tel:201221722221" underline="hover">
                <Typography className="ltr enFonts" noWrap>
                  {"+2 0122 172 2221"}
                </Typography>
              </Link>
            </Stack>
          </Toolbar>
        </Container>
      </AppBar>
      <AppBar position="static" variant="dense" color="primary" sx={{ mb: 1 }}>
        <Container disableGutters fixed>
          <Toolbar
            variant="dense"
            sx={{
              py: 1,
              gap: widthDevice(null, 1, 2, 3),
            }}
            disableGutters
          >
            {eList.fromArray(
              [
                {
                  n: t("home"),
                  i: <HomeTwoTone />,
                  u: "/",
                },
                {
                  n: t("store"),
                  i: <StorefrontTwoTone />,
                  u: "/products",
                },
                {
                  n: t("categories"),
                  i: <CategoryTwoTone />,
                  u: "/categories",
                },
                {
                  n: t("brands"),
                  i: <BeenhereTwoTone />,
                  u: "/brands",
                },
              ],
              (index, item) => (
                <Button
                  key={`hl-${index}`}
                  onClick={() => {
                    navigate(item.u);
                  }}
                  size="large"
                  variant="text"
                  color="inherit"
                  disableElevation
                  startIcon={item.i}
                >
                  {item.n}
                </Button>
              )
            )}
            <Box sx={{ flexGrow: 1 }} />
            <IconButton onClick={() => {}} color="inherit" size={"medium"}>
              <Badge badgeContent={favCount()} color="secondary">
                <FavoriteTwoTone />
              </Badge>
            </IconButton>

            <IconButton onClick={() => {}} color="inherit" size={"medium"}>
              <Badge badgeContent={cartCount()} color="secondary">
                <ShoppingCartTwoTone />
              </Badge>
            </IconButton>

            <IconButton
              onClick={() => {
                var lk = Object.keys(locales);
                var l = lk.indexOf(locale);
                lk.splice(l, 1);

                setLocale(lk[0]);
              }}
              color="inherit"
              size="medium"
            >
              <LanguageTwoTone />
            </IconButton>
          </Toolbar>
        </Container>
      </AppBar>
    </>
  );
};

const SearchBar = ({ t, fullWidth, height = "auto" }) => {
  return (
    <ButtonGroup fullWidth={fullWidth} sx={{ height: height }}>
      <Autocomplete
        options={[""]}
        renderInput={(params) => (
          <TextField
            {...params}
            type="search"
            variant="outlined"
            size="normal"
            placeholder={t("search_hint")}
            InputProps={{
              sx: {
                height: height,
                borderEndEndRadius: 0,
                borderStartEndRadius: 0,
              },
            }}
            sx={{ height: height }}
          />
        )}
        fullWidth={fullWidth}
        sx={{ height: height, borderRadius: 0 }}
        freeSolo={true}
      />
      <Button
        onClick={() => {}}
        variant="contained"
        size="large"
        color="primary"
        disableElevation
        fullWidth={false}
        sx={{ height: height }}
      >
        <SearchTwoTone />
      </Button>
    </ButtonGroup>
  );
};
