import { useEffect } from "react";
import { Box, Stack, Grid, Container } from "@mui/material";
import { State } from "../../models/core/my-react";
import { ebAPIs } from "../../configs/apis";
import AppScaffold from "../../layouts/AppScaffold";
import { useNavigate, useParams } from "react-router-dom";
import { useTranslation } from "../../configs/translation";
import ProductImages from "./layouts/ProductImages";
import ProductTitle from "./layouts/ProductTitle";
import ProductDesc from "./layouts/ProductDesc";
import ProductSimilar from "./layouts/ProductSimilar";

const ProductPage = () => {
  // navigation
  const navigate = useNavigate();
  const { t, forLocale } = useTranslation();
  const { productId } = useParams();
  const productData = State(null);
  const similarData = State(null);

  // product id change
  useEffect(() => {
    // no product, go home
    if (!productId) {
      navigate("/");
    }

    window.scrollTo(0, 0);
    productData.value = null;
    similarData.value = null;
    // eslint-disable-next-line
  }, [productId]);

  // get product from api
  useEffect(() => {
    (async function () {
      if (productData.value == null) {
        let r = await ebAPIs.callStored("public/ecommerce/products/details", {
          method: "POST",
          bodyData: { product_id: productId },
          namespace: productId,
        });
        if (r.success === false) {
          console.warn(r.message, r.data);
          alert(r.message);
        } else {
          productData.value = r.data;
        }
      }
    })();
  }, [productData, productId, navigate]);

  // get similar from api
  useEffect(() => {
    (async function () {
      if (similarData?.value == null) {
        let r = await ebAPIs.callStored("public/ecommerce/products/similar", {
          method: "POST",
          bodyData: { product_id: productId },
          namespace: productId,
        });
        if (r.success === false) {
          console.warn(r.message, r.data);
          alert(r.message);
        } else {
          similarData.value = r.data;
        }
      }
    })();
  }, [similarData, productId]);

  // return
  return (
    <AppScaffold
      contentScrollable={productData.value !== null}
      title={
        productData.value === null
          ? t("product")
          : productData.value?.product?.[forLocale({ ar: "name_alt" }, "name")]
      }
      main={false}
    >
      <Container disableGutters fixed>
        <Stack spacing={2}>
          <Box>
            <Grid container spacing={2}>
              <Grid item xs={12} md={6}>
                <ProductImages
                  product={productData.value?.product}
                  media={productData.value?.media}
                />
              </Grid>
              <Grid item xs={12} md={6}>
                <ProductTitle
                  productId={productId}
                  product={productData.value?.product}
                />
              </Grid>
            </Grid>
          </Box>
          <ProductDesc product={productData.value?.product} />
          <ProductSimilar products={similarData.value?.products} />
        </Stack>
      </Container>
    </AppScaffold>
  );
};
export default ProductPage;
