import { Box, Divider, Skeleton, Stack, Typography } from "@mui/material";
import { useTheme } from "@emotion/react";
import { useTranslation } from "../../../configs/translation";
import { ebAPIs } from "../../../configs/apis";
import MyTabSlider, {
  MyTabSliderImage,
  MyTabSliderLabel,
} from "../../../components/MyTabSlider";

const HomeBrands = ({ brands }) => {
  const theme = useTheme();
  const { t, forLocale } = useTranslation();

  if (!brands) {
    return (
      <Box px={1}>
        <Skeleton variant="rounded" height={65} />
      </Box>
    );
  } else if (brands?.length > 0) {
    return (
      <Stack direction="column" spacing={2} width={"100%"}>
        <Divider textAlign="left" sx={{ px: 1 }}>
          <Typography variant="h6">{t("brands")}</Typography>
        </Divider>
        <MyTabSlider
          data={brands}
          onChange={(index, item) => {
            console.log(index, item);
          }}
          label={(item) =>
            MyTabSliderLabel({
              text: item?.[forLocale({ ar: "name_alt" }, "name")],
            })
          }
          icon={(item) =>
            MyTabSliderImage({
              src: ebAPIs.getMedia(item?.thumbnail),
              alt: item?.[forLocale({ ar: "name_alt" }, "name")],
              width: 100,
              height: 40,
              borderColor: theme.palette.light.main,
            })
          }
          width={100}
        />
      </Stack>
    );
  } else {
    return null;
  }
};
export default HomeBrands;
