import { Box, Skeleton } from "@mui/material";
import { State } from "../models/core/my-react";
import { ImageNotSupportedTwoTone } from "@mui/icons-material";

const MyImage = ({
  src,
  alt,
  fallback,
  width = "100%",
  height = "100%",
  objectFit = "contain",
  lazy = true,
  draggable = false,
  bg,
  ...props
}) => {
  const loading = State(!!src);
  const error = State(!src);

  return (
    <Box
      sx={{
        ...(props?.style ?? {}),
        ...(props?.sx ?? {}),
        height,
        width,
        position: "relative",
        backgroundColor: bg,
        display: "block",
      }}
    >
      <img
        src={src}
        alt={alt}
        onLoad={() => {
          loading.value = false;
        }}
        onError={() => {
          loading.value = false;
          error.value = true;
        }}
        style={{
          ...(props?.style ?? {}),
          position: "absolute",
          height: "100%",
          width: "100%",
          inset: 0,
          objectFit,
          backgroundColor: bg,
          visibility: error.value ? "hidden" : "visible",
        }}
        loading={lazy ? "lazy" : "eager"}
        draggable={draggable}
      />
      {loading.value && (
        <Box
          sx={{
            position: "absolute",
            height: "100%",
            width: "100%",
            inset: 0,
            pointerEvents: "none",
            userSelect: "none",
            display: "flex",
            justifyContent: "center",
            alignItems: "center",
            backgroundColor: bg,
            overflow: "hidden",
          }}
        >
          <Skeleton variant="rectangle" height={height} width={width} />
        </Box>
      )}
      {!fallback && error.value && (
        <Box
          sx={{
            position: "absolute",
            height: "100%",
            width: "100%",
            inset: 0,
            pointerEvents: "none",
            userSelect: "none",
            display: "flex",
            justifyContent: "center",
            alignItems: "center",
            backgroundColor: bg,
            overflow: "hidden",
          }}
        >
          <ImageNotSupportedTwoTone color="primary2" fontSize={"large"} />{" "}
        </Box>
      )}
      {fallback && error.value && (
        <Box
          sx={{
            position: "absolute",
            height: "100%",
            width: "100%",
            inset: 0,
            display: "flex",
            justifyContent: "center",
            alignItems: "center",
            backgroundColor: bg,
            overflow: "hidden",
          }}
        >
          <img
            src={fallback}
            alt={`Fallback for ${alt}`}
            style={{
              ...(props?.style ?? {}),
              height,
              width,
              objectFit,
              backgroundColor: bg,
            }}
            draggable={draggable}
          />
        </Box>
      )}
    </Box>
  );
};
export default MyImage;
