import { useEffect } from "react";
import { Container, Stack } from "@mui/material";
import { State } from "../../models/core/my-react";
import { ebAPIs } from "../../configs/apis";
import AppScaffold from "../../layouts/AppScaffold";
import HomeCategories from "./layouts/HomeCategories";
import { useWidth } from "../../configs/breakpoints";
import HomeBrands from "./layouts/HomeBrands";
import HomeProducts from "./layouts/HomeProducts";
import { useTranslation } from "../../configs/translation";

const HomePage = () => {
  const homeData = State(null);
  const productsData = State(null);
  const { widthDevice } = useWidth();
  const { t } = useTranslation();

  // home categories
  useEffect(() => {
    (async function () {
      if (homeData.value == null) {
        let r = await ebAPIs.callStored("public/ecommerce/home", {
          method: "POST",
        });
        if (r.success === false) {
          console.log(r.message, r.data);
          alert(r.message);
        } else {
          homeData.value = r.data;
        }
      }
    })();
  }, [homeData]);

  // trending products
  useEffect(() => {
    (async function () {
      if (productsData.value == null) {
        let r = await ebAPIs.callStored("public/ecommerce/products/get", {
          method: "POST",
          bodyData: {
            light: 1,
            stock_only: 1,
            priced_only: 1,
            thresh: 12,
          },
          namespace: "trending_products",
        });
        if (r.success === false) {
          console.log(r.message, r.data);
          alert(r.message);
        } else {
          productsData.value = r.data;
        }
      }
    })();
  }, [productsData]);

  return (
    <AppScaffold>
      <Container disableGutters fixed>
        <Stack sx={{ my: widthDevice(2, null, 4) }} spacing={4}>
          <HomeCategories categories={homeData.value?.categories} />
          <HomeBrands brands={homeData.value?.brands} />
          <HomeProducts
            title={t("trending_products")}
            products={productsData.value?.products}
          />
        </Stack>
      </Container>
    </AppScaffold>
  );
};
export default HomePage;
