import {
  AddTwoTone,
  AddShoppingCartTwoTone,
  RemoveTwoTone,
  ShoppingCartTwoTone,
} from "@mui/icons-material";
import { Button, ButtonGroup, Typography } from "@mui/material";
import { useMyCart } from "../models/MyCart";
import { useTranslation } from "../configs/translation";

const AppProductCartBtn = ({
  sx,
  productId,
  product,
  full = true,
  size = "medium",
  fullWidth = false,
}) => {
  const { t } = useTranslation();
  const { getCartItem, addCartItem, subCartItem } = useMyCart();

  const isOutStock = !product?.stock || product?.stock === "0";
  const isReservable = isOutStock && product?.per_order === "1";

  if (getCartItem(productId)) {
    if (full) {
      return (
        <ButtonGroup
          variant="outlined"
          size={size}
          fullWidth={fullWidth}
          sx={sx}
        >
          <Button
            color="secondary"
            variant="contained"
            disableFocusRipple
            disableRipple
            disableElevation
            size={size}
          >
            <ShoppingCartTwoTone fontSize="small" />
          </Button>
          <Button
            onClick={() => {
              subCartItem(productId, product);
            }}
            size={size}
          >
            <RemoveTwoTone color="warning" fontSize="small" />
          </Button>
          <Button disableFocusRipple disableRipple size={size} sx={{ py: 0 }}>
            <Typography className="enFonts" variant="subtitle1">
              {getCartItem(productId)}
            </Typography>
          </Button>
          <Button
            onClick={() => {
              addCartItem(productId, product);
            }}
            size={size}
          >
            <AddTwoTone color="success" fontSize="small" />
          </Button>
        </ButtonGroup>
      );
    } else {
      return (
        <ButtonGroup
          variant="outlined"
          size={size}
          fullWidth={fullWidth}
          sx={sx}
        >
          <Button
            onClick={() => {
              subCartItem(productId, product);
            }}
            size={size}
          >
            <RemoveTwoTone color="warning" fontSize="small" />
          </Button>
          <Button disableFocusRipple disableRipple size={size} sx={{ py: 0 }}>
            <Typography className="enFonts" variant="subtitle1">
              {getCartItem(productId)}
            </Typography>
          </Button>
          <Button
            onClick={() => {
              addCartItem(productId, product);
            }}
            size={size}
          >
            <AddTwoTone color="success" fontSize="small" />
          </Button>
        </ButtonGroup>
      );
    }
  } else {
    return (
      <Button
        onClick={() => {
          addCartItem(productId, product);
        }}
        variant="contained"
        startIcon={<AddShoppingCartTwoTone fontSize={size} />}
        size={size}
        fullWidth={fullWidth}
        disableElevation
        sx={{
          px: 0.5,
          ...(sx ?? {}),
        }}
      >
        {isReservable ? t("reserve_in_cart") : t("add_to_cart")}
      </Button>
    );
  }
};
export default AppProductCartBtn;
