import { Card, Tab, Tabs, Typography, useTheme } from "@mui/material";
import { eList } from "../models/core/my-js";
import { State } from "../models/core/my-react";
import MyImage from "./MyImage";
import { forwardRef, useEffect } from "react";

const MyTabSlider = forwardRef(
  (
    {
      data,
      onChange = (index, item) => null,
      label = (item, selected) => null,
      icon = (item, selected) => null,
      scrollButtons = "auto",
      orientation = "horizontal",
      spacing = 1,
      borderRadius = 1,
      width,
      height,
      sx,
    },
    ref
  ) => {
    const val = State(0);

    // on mounted
    useEffect(() => {
      // reset current func
      if (ref?.current) {
        ref.current.setValue = (index) => {
          val.value = parseInt(index);
        };
        ref.current.value = val.value;
      }
      // eslint-disable-next-line
    }, []);

    return (
      <Tabs
        ref={ref}
        value={val.value}
        onChange={(evt, index) => {
          index = parseInt(index);
          val.value = index;
          onChange(index, data?.[index]);
        }}
        variant="scrollable"
        scrollButtons={scrollButtons}
        allowScrollButtonsMobile={true}
        orientation={orientation}
        sx={{
          width: orientation === "vertical" ? "auto" : "100%",
          height: orientation === "vertical" ? "100%" : "auto",
        }}
        TabIndicatorProps={{
          sx: { display: "none" },
        }}
        // TabScrollButtonProps={{ }}
      >
        {eList.fromArray(data, (i, obj) => {
          i = parseInt(i);
          return (
            <Tab
              key={`msv-${i}`}
              value={i}
              label={label && label(obj, i === val.value)}
              icon={icon && icon(obj, i === val.value)}
              sx={{
                ...(sx ?? {}),
                minWidth: "unset",
                maxWidth: "unset",
                width: width,
                height: height,
                padding: 0,
                mx: orientation === "vertical" ? 0 : spacing,
                my: orientation === "vertical" ? spacing : 0,
                alignSelf: "baseline",
                borderRadius: borderRadius,
                overflow: "hidden",
              }}
            />
          );
        })}
      </Tabs>
    );
  }
);
export default MyTabSlider;

export const MyTabSliderLabel = ({ text }) => {
  const theme = useTheme();

  return (
    <Typography
      variant="subtitle2"
      sx={{
        display: "-webkit-box",
        overflow: "hidden",
        WebkitBoxOrient: "vertical",
        WebkitLineClamp: 2,
        color: theme.palette.primary.main,
        opacity: 1,
      }}
      children={text}
    />
  );
};

export const MyTabSliderImage = ({
  src,
  alt,
  width,
  height,
  borderRadius = 1,
  objectFit = "cover",
  borderColor = "inherit",
}) => {
  const theme = useTheme();
  return (
    <Card
      variant="outlined"
      sx={{
        width: width,
        height: height,
        overflow: "hidden",
        borderWidth: 2,
        borderRadius: borderRadius,
        bgcolor: theme.palette.white.main,
        borderColor: borderColor,
      }}
    >
      <MyImage
        bg="white"
        width="100%"
        height="100%"
        objectFit={objectFit}
        src={src}
        alt={alt}
        draggable={false}
      />
    </Card>
  );
};
